import React from 'react'
import './EditPersonalData.scss'
import { Field, Form, Formik, FormikProps } from 'formik';

function EditPersonalData () {

    return (
        <div className='personalData container'>
            <aside className='personalData__sidebar'>
                <div className='partnerPhoto'></div>
                <div className='partnerName'>Иван Иванов</div>
                <div className='orgName'>НКО &quot;Кошкин Дом&quot;</div>
                <div className='accountControls'>
                    <div className='myData'>Мои данные</div>
                    <div className='accountSettings'>Настройки аккаунта</div>
                    <div className='orgData'>Данные организации</div>
                    <div className='usersControl'>Управление пользователями</div>
                    <div className='myTasks'>Мои задачи</div>
                    <div className='subscriptions'>Подписки</div>
                    <div className='notifications'>Уведомления</div>
                    <div className='deleteAccount'>Удаление аккаунта</div>
                </div>
            </aside>
            <div className='personalData__personalDataDetails'>
                <h2 className='personalData__personalDataDetails_title'>Редактировать данные:</h2>

                <Formik
                    initialValues={{ email: '', color: 'red', firstName: '', lastName: '' }}
                >
                    {() => (
                        <Form>
                            <Field name="firstName" placeholder="Doe"  />
                            <Field name="lastName" placeholder="Doe"  />

                        </Form>
                    )}
                </Formik>
            </div>
        </div>
    )
}

export default EditPersonalData